.project {
   max-width: 350px !important;
   margin: auto !important;
   margin-top: 30px !important;
}
.projects__links {
   display: flex;
   justify-content: space-around;
   align-items: center;
   margin-top: 10px;
   box-shadow: 0;
   /* border: 1px solid red; */
}
.projects__links a {
   text-decoration: none !important;
   font-weight: 500;
   padding: 5px 8px;
   border: 1px solid #ccc;
   border-radius: 5px;
   color: #5f1b20;
}
